<template>
  <div class="max-w500 mg0auto h100vh" :style="`background:#f8f8f8 url(${img.initBg}) no-repeat; background-size:100% 100%;`">
    <header class="flex j-content-sb pd10">
      <div class="flex a-items-c">
        <img :src="img.tier[0]" alt="등급" class="mg-r5 v-middle" width="30"/>
        <strong class="v-middle">최대여섯글자</strong>
      </div>
      <div class="flex a-items-c">
        <!-- <img :src="img.heart" alt="heart" width="20" class="v-middle"/> -->
        <strong class="v-middle">&#9829;&Cross;<span>3</span></strong>
      </div>
    </header>
    <section class="pd10 pd-t0" style="display:grid; grid-template-columns:1fr 1fr; gap:10px; grid-gap:10px; grid-template-rows:repeat(3, 80px);">
      <article class="border-ccc radius10 flex a-items-c j-content-c bg-eee" style="box-shadow:0 1px 1px #bbb;">스테이지모드</article>
      <article class="border-ccc radius10 flex a-items-c j-content-c bg-eee" style="box-shadow:0 1px 1px #bbb;">랭킹모드</article>
      <router-link to="/playGame" class="decoration-none color-000 border-ccc radius10 flex a-items-c j-content-c bg-fff" style="box-shadow:0 1px 1px #bbb;">
        <article>연습모드</article>
      </router-link>
      <article class="border-ccc radius10 flex a-items-c j-content-c bg-eee" style="box-shadow:0 1px 1px #bbb;">상점</article>
      <article class="border-ccc radius10 flex a-items-c j-content-c bg-eee" style="box-shadow:0 1px 1px #bbb;">설정</article>
      <article class="border-ccc radius10 flex a-items-c j-content-c bg-eee" style="box-shadow:0 1px 1px #bbb;"></article>
    </section>
  </div>
</template>

<script>
import initBg from '@/assets/img/initPage_bg.png';
import heart from '@/assets/img/heart.png';

//티어
import bronze from '@/assets/img/tier/bronze.png';
import silver from '@/assets/img/tier/silver.png';
import gold from '@/assets/img/tier/gold.png';
import platinum from '@/assets/img/tier/platinum.png';
import diamond from '@/assets/img/tier/diamond.png';
import master from '@/assets/img/tier/master.png';
import graduator from '@/assets/img/tier/graduator.png';


export default {
  name: 'lotto-offline',
  props: {
  },
  data: () => {
    return {
      img: {
        initBg: initBg,
        heart: heart,
        tier: {
          0: bronze,
          1: silver,
          2: gold,
          3: platinum,
          4: diamond,
          5: master,
          6: graduator
        }
      },
      ballColors: ['#F15A5A', '#F0C419', '#4EBA6F', '#2D95BF', '#955BA5'],
      pickedBalls: [],
      colAlphabet: 'ABCDEFGHI',

      btnType: 'check',
      btnState: {},
      isTestBtn: false,

      randomNumArr7: [],
      baseHint: '',
      moreHint: '',

      isShowNumberBoard: false,
      isShowHint: true,

      questLevel: {
        row: {},
        col: {}
      },

      finishedNumArr: [],
      finishedNumCnt: 0,

      isShowDifficulty: false,
      starCnt: 0,
      setStarCnt: 0,

      setInterval: '',
      timer: 0,
      timerColor: '#5af181',

      gameLevel: {
        arr45: [],
        arr6: [],
        arr7: [],
        row: {},
        col: {},

      },
    }
  },

  methods: {
    initLotto(boolean) {
      if(boolean || confirm('로또 숫자를 초기화 하시겠습니까?')){
        const t = this;
        t.btnType = 'check';
        t.pickedBalls = [];
        t.btnState = {};
        t.isTestBtn = false;
        
  
        document.querySelectorAll('[data-symbol]').forEach((currentValue) => {
          currentValue.classList.add('none');
        })
  
        clearInterval(t.setInterval);
        t.timer = 0;
        t.timerColor = '#5af181';

        let rdmNum = t.getRandomNum();
        if(t.setStarCnt > 0 && rdmNum != t.setStarCnt){
          return t.initLotto(true);
        }
  
        t.setInterval = setInterval(() => {
          t.timer++;

          if(t.timer > 109){
            t.timerColor = '#f55222';
          }else if(t.timer > 59){
            t.timerColor = '#f5e67e';
          }

          if(t.timer == 120){
            clearInterval(t.setInterval);
            return t.initLotto();
          }
        }, 1000);
      }
    },

    initNum() {
      if(confirm('숫자판을 초기화 하시겠습니까?')){
        const t = this;
        t.btnType = 'check';
        t.pickedBalls = [];
        t.btnState = {};
        t.isTestBtn = false;

        document.querySelectorAll('[data-symbol]').forEach((currentValue) => {
          currentValue.classList.add('none');
        })
      }
    },

    getRandomNum() {
      const t = this;
      
      const getShuffleArr = (arr, length, shuffledArr) => {
        const random = (min, max) => {
          return Math.floor((Math.random() * (max - (min - 1)) + min));
        }
        let tmpArr = arr;
        let tmpShuffledArr = shuffledArr == undefined ? [] : shuffledArr;

        tmpShuffledArr.push(tmpArr.splice(random(0, tmpArr.length - 1), 1)[0]);

        if(tmpShuffledArr.length !== length) return getShuffleArr(tmpArr, length, tmpShuffledArr);

        return tmpShuffledArr;
      }

      let arr45 = [];
      for(let i = 0; i < 45; i++){
        arr45.push(i + 1);
      }

      let shuffledArr = getShuffleArr(arr45, 7);
      shuffledArr[0] = '+ ' + shuffledArr[0];

      t.randomNumArr7 = [];
      t.randomNumArr7 = shuffledArr.sort(function(a, b)  {
        if(a > b) return 1;
        if(a === b) return 0;
        if(a < b) return -1;
      });

      t.randomNumArr7.push(t.randomNumArr7.shift());  //첫번째와 마지막번째 위치 변경

      t.baseHint = '';
      t.moreHint = '';

      t.getBaseHint();
      t.getMoreHint();

      let questLevel = t.getQuestLevel();
      return questLevel;
    },

    showNumberBoard() {
        const t = this;
        t.isShowNumberBoard = !t.isShowNumberBoard;
        t.pickedBalls = [];
        t.isTestBtn = false;
        
        t.setInterval = setInterval(() => {
          t.timer++;

          if(t.timer > 109){
            t.timerColor = '#f55222';
          }else if(t.timer > 59){
            t.timerColor = '#f5e67e';
          }
  
          if(t.timer == 120){
            clearInterval(t.setInterval);
            t.initLotto();
          }
        }, 1000);


    },

    getBaseHint() {
      const t = this;
      let tmpArr = [];
      for(let i = 0; i < 6; i++){
        tmpArr.push(t.randomNumArr7[i]);
      }
      let ballColors = [];

      tmpArr.forEach((el) => {
        //숫자 컬러
        if(el < 10){ballColors.push(t.ballColors[0])}
        else if(el < 19){ballColors.push(t.ballColors[1])}
        else if(el < 28){ballColors.push(t.ballColors[2])}
        else if(el < 37){ballColors.push(t.ballColors[3])}
        else{ballColors.push(t.ballColors[4])}

      })

      t.hint += '<p>';
      for(let i = 0; i < ballColors.length; i++){
        t.baseHint += `<strong class="inline-block radius50p border-box font11 color-fff mg-r3" style="width:7.8vw; height:7.8vw; max-width:60px; max-height:60px; background:${ballColors[i]};">
          <span class="vertical-m">?<span>
        </strong>`;
      }
      t.hint += '</p>';
    },

    getMoreHint() {
      const t = this;
      let tmpArr = [];
      let odds = 0;
      let total = 0;
      let multiTotal = 1;
      let eachNum = []; //각 숫자별 자릿수의 차

      for(let i = 0; i < 6; i++){
        tmpArr.push(t.randomNumArr7[i]);
      }

      let arrToStr = tmpArr.join('').split('').sort().join('');
      let colomns = '';

      tmpArr.forEach((el) => {
        if(el % 2 == 1){  //홀짝
          odds++;
        }
        total += el;  //합계
        multiTotal *= el; //각숫자의 곱

        let alphabets = ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I'];
        for(let i = 0 ; i < 9; i++){
          if(el == (1 + i) || el == (10 + i) || el == (19 + i) || el == (28 + i) || el == (37 + i)){
            colomns += alphabets[i];
          }
        }
        //각 숫자별 자릿수의 차
        if(String(el).length == 1){
          eachNum.push(0);
        }else{
          let numValue = String(el)[0] - String(el)[1];
          eachNum.push(Math.abs(numValue)); 
        }

      })

      eachNum.sort();

      colomns = colomns.split('').sort().join('');

      let duplStr = colomns[0];
      let duplCnt = 0;
      let changeStr = colomns[0];

      for(let i = 0; i < colomns.length; i++){
        if(colomns[i] == duplStr){
          duplCnt++;
        }else{
          changeStr += duplCnt + colomns[i];
          duplCnt = 1;
        }
        duplStr = colomns[i];
        if(i == (colomns.length - 1)){
          changeStr += duplCnt;
        }
      }

      //출력

      t.moreHint += `<p class="mg0 mg-b10">
      홀: ${odds}개, 짝: ${tmpArr.length - odds}개
      </p>`;
      t.moreHint += `<p class="mg0 mg-b10">
      숫자구성: ${arrToStr}
      </p>`;
      t.moreHint += `<p class="mg0 mg-b10">
      모든 수의 합: ${total}
      </p>`;
      t.moreHint += `<p class="mg0 mg-b10">
      ${changeStr}
      </p>`;
      t.moreHint += `<p class="mg0 mg-b10">
      모든 숫자의 곱: ${multiTotal}
      </p>`;

    },

    showMoreHint() {
      const t = this;
      if(t.isShowHint) return;
      if(confirm('힌트를 보려면 하트가 소진됩니다.\n진행하시겠습니까?')){
        t.isShowHint = true;
      }
    },

    pickTheBall(target) {
      const t = this;

      let parent = target.target.closest('span');
      let btnNum = parent.querySelector('span').innerText;
      let tmpArr = t.pickedBalls;

      // parent.querySelectorAll('[data-symbol]').forEach((currentValue) => {
      //   currentValue.classList.add('none');
      // })

      if(t.btnState[btnNum] == (t.isTestBtn + t.btnType)){
        if(t.btnType == 'check'){
          let elemIdx = tmpArr.indexOf(Number(btnNum));
          if (elemIdx > -1){
            tmpArr.splice(elemIdx, 1);
          }
        }

        parent.querySelectorAll('[data-symbol]').forEach((currentValue) => {
          currentValue.classList.add('none');
        })

        parent.querySelector('[data-symbol="' + (t.isTestBtn + t.btnType) + '"]').classList.add('none');
        t.btnState[btnNum] = false;

      }else{
        if(t.btnType == 'check'){
          if(tmpArr.length > 5 && String(t.btnState[btnNum]).indexOf('check') == -1){
            alert('6개를 넘을 수 없습니다.');
            return;
          }

          if(String(t.btnState[btnNum]).indexOf('check') == -1 || !t.btnState[btnNum]){
            tmpArr.push(Number(btnNum));
          }
        }else{
          let elemIdx = tmpArr.indexOf(Number(btnNum));
          if (elemIdx > -1){
            tmpArr.splice(elemIdx, 1);
          }
        }

        parent.querySelectorAll('[data-symbol]').forEach((currentValue) => {
          currentValue.classList.add('none');
        })

        parent.querySelector('[data-symbol="' + (t.isTestBtn + t.btnType) + '"]').classList.remove('none');
        t.btnState[btnNum] = (t.isTestBtn + t.btnType);
      }

      if(tmpArr.length > 0){
        t.pickedBalls = tmpArr.sort(function(a, b)  {
          if(a > b) return 1;
          if(a === b) return 0;
          if(a < b) return -1;
        });
      }

    },

    submitTheNumber() {
      const t = this;
      if(t.pickedBalls.length != 6){
        alert('6개의 숫자를 선택해주세요.');
        return;
      }

      let matchCnt = 0;
      let matchBonus = false;

      if(confirm(t.pickedBalls + '\n위 숫자로 제출하시겠습니까?')){
        for(let i = 0; i < t.pickedBalls.length; i++){
          for(let j = 0; j < t.randomNumArr7.length; j++){
            if(j == (t.randomNumArr7.length - 1)){
              if(t.pickedBalls[i] == t.randomNumArr7[j].substr(2, t.randomNumArr7[j].length)){
                matchBonus = true;
              }
            }else{
              if(t.pickedBalls[i] == t.randomNumArr7[j]){
                matchCnt++;
                break;
              }
            }
          }
        }

        switch(matchCnt){
          case 3:
            alert('남은시간: ' + (120 - t.timer) + '\n' + t.randomNumArr7 + '\n5등 당첨!\n나의 숫자\n' + t.pickedBalls);
          break;
          case 4:
            alert('남은시간: ' + (120 - t.timer) + '\n' + t.randomNumArr7 + '\n4등 당첨!\n나의 숫자\n' + t.pickedBalls);
          break;
          case 5:
            if(matchBonus){
              alert('남은시간: ' + (120 - t.timer) + '\n' + t.randomNumArr7 + '\n2등 당첨!\n나의 숫자\n' + t.pickedBalls);
            }else{
              alert('남은시간: ' + (120 - t.timer) + '\n' + t.randomNumArr7 + '\n3등 당첨!\n나의 숫자\n' + t.pickedBalls);
            }
          break;
          case 6:
            alert('남은시간: ' + (120 - t.timer) + '\n' + t.randomNumArr7 + '\n1등 당첨!\n나의 숫자\n' + t.pickedBalls);
          break;
          default:
            alert('남은시간: ' + (120 - t.timer) + '\n' + t.randomNumArr7 + '\n꽝!\n나의 숫자\n' + t.pickedBalls);
            break;
        }

        t.initLotto();
      }
    },

    getQuestLevel() {
      const t = this;
      const lv = t.gameLevel;

      //초기값
      lv.arr7 = JSON.parse(JSON.stringify(t.randomNumArr7));  //깊은복사
      lv.arr45 = [];
      lv.arr6 = [];
      for(let i = 0; i < 45; i++) lv.arr45.push(i + 1);
      for(let i = 0; i < 6; i++) lv.arr6.push(lv.arr7[i]);
      
      const lv1 = () => {
        const t = this;
        const lv = t.gameLevel;
        //초기화
        lv.row = {};
        lv.col = {};

        t.removeRow();
        t.removeCol();
        t.removeNoNum();

        let chkedArr = t.chkRemainNum(lv.row, lv.col, lv.arr45);
        if(chkedArr.length && lv.arr6.length) return lv1();

        return lv.arr6;
      }

      const lv2 = () => {
        const t = this;
        const lv = t.gameLevel;
        if(!lv.arr6.length) return;

        if(t.removeNoNum2().length && lv.arr6.length){
          lv1();
          return lv2();
        }

      }

      if(!lv1().length){
        return t.starCnt = 1;
      }else{
        // alert();
        lv2();
      }

      if(lv.arr6.length > 5){
        t.starCnt = 5;
      }else if(lv.arr6.length > 4){
        t.starCnt = 4;
      }else if(lv.arr6.length > 2){
        t.starCnt = 3;
      }else{
        t.starCnt = 2;
      }

      console.log(t.starCnt);
      console.log(lv.arr6);

      return t.starCnt;
      
    },

    getStrCntObj(str, loopCnt, obj) {
      if(isNaN(str)){
        console.log('String이 아닙니다.');
        return false;
      }
      
      const t = this;
      let cnt = loopCnt ? loopCnt : 0;
      let tmpObj = obj ? obj : {};

      if(tmpObj[str[cnt]]){
        tmpObj[str[cnt]]++;
      }else{
        tmpObj[str[cnt]] = 1;
      }

      if(++cnt != str.length) return t.getStrCntObj(str, cnt, tmpObj);

      return tmpObj;
    },

    removeRow() { //행지우기(색깔)
      const t = this;
      const lv = t.gameLevel;

      // let removeCnt = 0;
      for(let i = 0 ; i < 5; i++){
        let hasNumCnt = 0;

        for(let j = 0; j < 45; j++){
          if((i * 9) < lv.arr6[j] && lv.arr6[j] < ((i * 9) + 10)){
            hasNumCnt++;
          }
        }

        lv.row[i] = hasNumCnt;

        if(hasNumCnt == 0){
          // lv.arr45.splice(((i * 9) - removeCnt), 9);
          // removeCnt += 9;

          for(let k = 0; k < 9; k++){
            // let elIdx = lv.arr45.indexOf(i + (k * 9) + 1);
            let elIdx = lv.arr45.indexOf((i * 9) + k + 1);
            if(elIdx > -1){
              lv.arr45.splice(elIdx, 1);
            }
          }
        }
      }

    },

    removeCol() { //열지우기(abc)
      const t = this;
      const lv = t.gameLevel;
      for(let i = 0; i < 9; i++){
        let hasNumCnt = 0;
        for(let j = 0; j < lv.arr6.length; j++){
          if(lv.arr6[j] == (i + 1)
          || lv.arr6[j] == (i + 10)
          || lv.arr6[j] == (i + 19)
          || lv.arr6[j] == (i + 28)
          || lv.arr6[j] == (i + 37)){
            hasNumCnt++;
          }
        }
        lv.col[i] = hasNumCnt;

        if(hasNumCnt == 0){
          for(let k = 0; k < 5; k++){
            let elIdx = lv.arr45.indexOf(i + (k * 9) + 1);
            if(elIdx > -1){
              lv.arr45.splice(elIdx, 1);
            }
          }
        }
      }
    },

    removeNoNum() { //없는 번호 지우기
      const t = this;
      const lv = t.gameLevel;
      let arrToStr = lv.arr6.join('').split('').sort().join('');
      let elObj = t.getStrCntObj(arrToStr);

      for(let i = 0; i < 10; i++){
        if(!elObj[i]){
          let arr45Leng = lv.arr45.length;
          let delCnt = 0;
          for(let j = 0 ; j < arr45Leng; j++){ //for문 가끔 누락...?
            let hasNoNumIdx = String(lv.arr45[j + delCnt]).indexOf(i);
            if(hasNoNumIdx > -1){
              lv.arr45.splice(j + delCnt, 1);
              delCnt--;
            }
          }
        }
      }
    },

    removeNoNum2() {
      //056789 구성(중급난이도)
      const t = this;
      const lv = t.gameLevel;
      let arrToStr = lv.arr6.join('').split('').sort().join('');
      let elObj = t.getStrCntObj(arrToStr);
      let num056789Arr = [];
      let valid056789NumArr = [];

      for(let key in elObj){
        if(key == 0 || key > 4){
          let tmpNum056789Arr = [];
          for(let i = 0 ; i < lv.arr45.length; i++){
            if(String(lv.arr45[i]).substr(-1) == key){
              num056789Arr.push(lv.arr45[i]);
              tmpNum056789Arr.push(lv.arr45[i]);
            }
          }
          if(elObj[key] >= tmpNum056789Arr.length){

            tmpNum056789Arr.forEach((el) => {
              let matchedNum = lv.arr45.indexOf(el);
              let matchedNum2 = lv.arr6.indexOf(el);
              if(matchedNum > -1){
                let finishedNum =  lv.arr45.splice(matchedNum, 1);
                t.finishedNumCnt += finishedNum.length;
                t.finishedNumArr.push(finishedNum[0]);

                for(let i = 0; i < 9; i++){
                  if(i < 5){
                    if(i * 9 < el && el < (i * 9) + 10){  //row(색깔)
                      lv.row[i]--;
                    }
                  }

                  if(el == (i + 1)
                  || el == ((i + 1) + 9)
                  || el == ((i + 1) + 18)
                  || el == ((i + 1) + 27)
                  || el == ((i + 1) + 36)){ //col(ABC)
                    lv.col[i]--;
                  }
                }
              }

              if(matchedNum2 > -1){
                lv.arr6.splice(matchedNum2, 1);
              }
            })

            valid056789NumArr.push(key);

          }
        }
      }
      return valid056789NumArr;
    },


    chkRemainNum (row, col, remainedNum) {
        const t = this;
        let confirmedNumArr = [];
        for(let key in row){  //row(색깔)
          let tmpConfirmedNumArr = [];
          remainedNum.forEach((el) => {
            if(Number(key) * 9 < el && el < (Number(key) * 9) + 10){
              tmpConfirmedNumArr.push(el);
            }
          })

          if(row[key] >= tmpConfirmedNumArr.length){
            tmpConfirmedNumArr.forEach((el) => {
              confirmedNumArr.push(el);
            })
          }
        }

        for(let key in col){  //col(abc)
          let tmpConfirmedNumArr = [];
          remainedNum.forEach((el) => {
            if(el == (Number(key) + 1)
            || el == ((Number(key) + 1) + 9)
            || el == ((Number(key) + 1) + 18)
            || el == ((Number(key) + 1) + 27)
            || el == ((Number(key) + 1) + 36)){
              tmpConfirmedNumArr.push(el);
            }
          })

          if(col[key] >= tmpConfirmedNumArr.length){
            tmpConfirmedNumArr.forEach((el) => {
              if(confirmedNumArr.indexOf(el) == -1){
                confirmedNumArr.push(el);
              }
            })
          }
        }

        if(confirmedNumArr.length){
          // alert('있어');
          confirmedNumArr.forEach(function(el){
            let numIdx45 = t.gameLevel.arr45.indexOf(el);
            let numIdx6 = t.gameLevel.arr6.indexOf(el);
            t.gameLevel.arr45.splice(numIdx45, 1);
            t.gameLevel.arr6.splice(numIdx6, 1);
            row[parseInt(el / 9.1)] -= 1;
            col[(el % 9) - 1] -= 1;
          });

          // return t.chkRemainNum(row, col, remainedNum);
        }

        return confirmedNumArr;

      }
  },

  created() {
    const t = this;
    for(let i = 0; i < 45; i++){
      t.btnState[i + 1] = false;
    }
  },
  mounted() {
    const t = this;
    t.getRandomNum();
  }
}
</script>

